import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import './styles.scss';
import { selectDarkToggle } from '../../store/NavigationBar/selectors';
import { updateTheme, saveTheme } from '../../store/NavigationBar/actions';
import logo from './sitelogo.svg';
import { setPopUp } from '../../store/DossierSelectionPopUp/actions';
import useAuth from '../../hooks/useAuth';
import oidcConfig from '../../oidcConfig';

// Component containing the navigation bar with sign up options
const NavigationBar = function ({ onUpdateTheme, propsTheme, onSaveTheme, setIsOpenPopUp }) {
  const auth = useAuth();

  const onLogout = () => {
    auth.signout();
  };

  return (
    <div className={propsTheme ? 'navigation-bar dark' : 'navigation-bar'}>
      <header>
        <div className={propsTheme ? 'menu dark' : 'menu'}>
          <div className="logo">
            <a href="https://www.microstrategy.com/en" target="_blank" rel="noreferrer">
              <img alt="" src={logo} />
            </a>
          </div>
          <div className="title">Embedding SDK Playground (Beta)</div>
          <button
            data-tip
            data-for="button__start-over__tooltip"
            className="button__start-over"
            onClick={() => setIsOpenPopUp(true)}
          >
            <span className="material-icons">refresh</span>
            <p>Start over</p>
          </button>
          <div className="toggle">
            <label className="switch">
              <input
                type="checkbox"
                checked={propsTheme}
                onChange={(e) => {
                  onUpdateTheme(e.target.checked);
                  onSaveTheme(e.target.checked);
                }}
              />
              <span className="slider round" />
            </label>
          </div>
          {oidcConfig.requireAuth && (
            <div className="button-logout" onClick={onLogout}>
              <span className="material-icons">logout</span>
              Logout
            </div>
          )}
        </div>
      </header>
    </div>
  );
};

function mapStateToProps(state) {
  return {
    propsTheme: selectDarkToggle(state),
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      onUpdateTheme: updateTheme,
      onSaveTheme: saveTheme,
      setIsOpenPopUp: setPopUp,
    },
    dispatch,
  );
}

export default connect(mapStateToProps, mapDispatchToProps)(NavigationBar);
