import initialState from '../../initialState';
import { OperationsMenuActions } from '../../constants';

const reducer = (state = initialState.OperationsMenu, action = {}) => {
  switch (action.type) {
    case OperationsMenuActions.SWITCH_DISPLAY:
      return {
        ...state,
        op: action.op,
        minOp: action.minOp,
        codeAndEvents: action.codeAndEvents,
      };
    default:
      return state;
  }
};
export default reducer;
