import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Console, Hook, Unhook, Decode } from 'console-feed';

import './styles.scss';
import { selectContent } from '../../store/EventPanel/selectors';
import { selectContent as selectCodeContent } from '../../store/CodePanel/selectors';
import { selectDarkToggle } from '../../store/NavigationBar/selectors';
import { selectReload } from '../../store/PreviewPanel/selectors';
import ButtonTooltip from '../Tooltip/buttonTooltip';

const EventPanel = function ({ propsContent, propsTheme, codeContent, reload }) {
  const eventTab = 'eventTab';
  const consoleTab = 'consoleTab';
  const [eventContent, setContent] = useState('');
  const [logs, setLogs] = useState([]);
  const [tab, setTab] = useState('eventTab');
  const [showRedCircle, setShowRedCircle] = useState(false);

  // Listen to the console
  useEffect(() => {
    Hook(
      window.console,
      (log) => {
        const decodedLog = Decode(log);
        setLogs((logs) => [...logs, decodedLog]);
        if (decodedLog.method === 'error' && !showRedCircle) {
          setShowRedCircle(true);
        }
      },
      true,
    );
    return () => Unhook(window.console);
  }, []);

  // Update the event handler tab
  useEffect(() => {
    setContent((eventContent) => eventContent + propsContent);
  }, [propsContent]);

  // Every time new code is run, the console and event handler should be cleared
  useEffect(() => {
    setContent('');
    setLogs([]);
  }, [codeContent]);

  // Every time the same code is run again, the console and event handler should be cleared
  useEffect(() => {
    if (reload) {
      setContent('');
      setLogs([]);
    }
  }, [reload]);
  const panel = () => {
    if (tab === eventTab) {
      return (
        <pre>
          <code>{eventContent}</code>
        </pre>
      );
    }
    return (
      <Console
        logs={logs}
        styles={{
          BASE_FONT_FAMILY: "source-code-pro, Menlo, Monaco, Consolas, 'Courier New',monospace",
          LOG_COLOR: 'inherit',
          LOG_BACKGROUND: 'transparent',
          BASE_COLOR: 'inherit',
          BASE_BACKGROUND_COLOR: 'transparent',
          LOG_ERROR_BACKGROUND: 'transparent',
          LOG_ERROR_BORDER: 'none',
          LOG_ERROR_COLOR: 'rgb(233,63,59)',
        }}
        filter={['log', 'error']}
      />
    );
  };
  return (
    <div
      className={propsTheme ? 'events-container card-shadow dark' : 'events-container card-shadow '}
    >
      <div className="event-container__header card-header">
        <div className="tabs">
          <div
            className={tab === eventTab ? 'tab-selected' : 'tab'}
            onClick={() => setTab(eventTab)}
          >
            Events
          </div>
          <div
            className={tab === consoleTab ? 'tab-selected' : 'tab'}
            onClick={() => {
              setTab(consoleTab);
              setShowRedCircle(false);
            }}
          >
            Console
            <span className={showRedCircle ? 'red-circle' : 'red-circle hide'} />
          </div>
        </div>
        <button
          data-tip
          data-for="clear_events-tooltip"
          className="clear_events"
          onClick={() => {
            if (tab === eventTab) {
              setContent('');
            } else {
              setLogs([]);
              setShowRedCircle(false);
            }
          }}
        >
          <span className="material-icons">delete</span>
        </button>
        <ButtonTooltip
          id="clear_events-tooltip"
          getContent={() => <div className="tooltip-text">Clear log</div>}
        />
      </div>
      <div
        className="event-container__text"
        onClick={() => {
          if (tab === consoleTab) {
            setShowRedCircle(false);
          }
        }}
      >
        {panel()}
      </div>
    </div>
  );
};

function mapStateToProps(state) {
  return {
    propsContent: selectContent(state),
    propsTheme: selectDarkToggle(state),
    codeContent: selectCodeContent(state),
    reload: selectReload(state),
  };
}

export default connect(mapStateToProps, null)(EventPanel);
