import initialState from '../../initialState';
import { DossierSelectionPopUpActions } from '../../constants';

const reducer = (state = initialState.DossierSelectionPopUp, action = {}) => {
  switch (action.type) {
    case DossierSelectionPopUpActions.SET_POPUP:
      return { ...state, popUp: action.popUp };
    default:
      return state;
  }
};
export default reducer;
