import React from 'react';
import ReactTooltip from 'react-tooltip';
// ...props will override defaults
const ButtonTooltip = function (props) {
  return (
    <ReactTooltip
      id="placeholder"
      className="react-tooltip"
      place="bottom"
      effect="solid"
      border={false}
      clickable
      multiline
      getContent={() => <div className="tooltip-text">Place holder text</div>}
      {...props}
    />
  );
};

export default ButtonTooltip;
