import { createSelector } from 'reselect';

export const selectOperationsMenu = (state) => state.OperationsMenu;
export const selectOp = createSelector(selectOperationsMenu, (OperationsMenu) => OperationsMenu.op);
export const selectMinOp = createSelector(
  selectOperationsMenu,
  (OperationsMenu) => OperationsMenu.minOp,
);
export const selectCodeAndEventsNoOp = createSelector(
  selectOperationsMenu,
  (OperationsMenu) => OperationsMenu.codeAndEvents,
);
