import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import OperationItem from './OperationItem';
import { operations } from '../../constants';
import { selectDarkToggle } from '../../store/NavigationBar/selectors';
import '../../variables.scss';
import './styles.scss';

// Component containing operations that embedding sdk has. Each operation item can be dragged into the code editor to display
// the code to execute the operation.

const OperationsMenu = function ({ isDark }) {
  // The search term in search bar
  const [searchTerm, setSearchTerm] = useState('');

  // Add operations in a subcategory under operations menu
  const buildDomFromOps = (ops, dropdown = false) => {
    return ops.map((opGroup) => {
      return (
        <details open={dropdown} key={opGroup.groupName}>
          <summary>{opGroup.groupName}</summary>
          <ul className="operation-item-list">
            {opGroup.operationList.map((op) => {
              return <OperationItem op={op} key={op.displayName} isDark={isDark} />;
            })}
          </ul>
        </details>
      );
    });
  };
  const operationsDom = buildDomFromOps(operations);
  const [displayDom, setDisplayDom] = useState(operationsDom);

  useEffect(() => {
    if (searchTerm !== '') {
      const filteredOps = [];
      operations.forEach((opGroup) => {
        const newOpGroup = { groupName: opGroup.groupName };
        newOpGroup.operationList = opGroup.operationList.filter((op) =>
          op.displayName.toLowerCase().includes(searchTerm.toLowerCase()),
        );
        if (newOpGroup.operationList.length > 0) {
          filteredOps.push(newOpGroup);
        }
      });
      setDisplayDom(buildDomFromOps(filteredOps, true));
    } else {
      setDisplayDom(operationsDom);
    }
  }, [searchTerm]);

  return (
    <div className={isDark ? 'operations-menu dark' : 'operations-menu'}>
      <div className="head">Operations</div>

      <div className="search-bar">
        <input
          type="search"
          name="Search"
          className="search-input"
          placeholder="Search Operations"
          onChange={(event) => {
            setSearchTerm(event.target.value);
          }}
        />
        <span className="material-icons">search</span>
      </div>

      <div className="details-wrapper">{displayDom}</div>
      <div className="link">
        <a
          className="link-text"
          href="https://github.com/MicroStrategy/playground"
          target="_blank"
          rel="noreferrer"
        >
          Playground User Manual
        </a>
        <br />
        <a
          className="link-text"
          href="https://www2.microstrategy.com/producthelp/Current/EmbeddingSDK/Content/topics/Intro_to_the_Embedding_SDK.htm"
          target="_blank"
          rel="noreferrer"
        >
          Embedding SDK Documentation
        </a>
      </div>
    </div>
  );
};
function mapStateToProps(state) {
  return {
    isDark: selectDarkToggle(state),
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(OperationsMenu);
