/**
 * Equivalent is:
 * new RegExp('(https://[-a-zA-Z0-9@:%_.~#=+]+)/([-a-zA-Z0-9@:%_.~#=+]+)/app/([A-Z0-9]+)/([A-Z0-9]+)',);
 */
export const dossierUrlRegExp =
  /(?<baseServerUrl>https:\/\/[-a-zA-Z0-9@:%_.~#=+]+)\/(?<libraryName>[-a-zA-Z0-9@:%_.~#=+]+)\/app\/(?<projectID>[A-Z0-9]+)\/(?<dossierID>[A-Z0-9]+)/;
