import React, { useEffect } from 'react';
import {
  TokenRequest,
  BaseTokenRequestHandler,
  GRANT_TYPE_AUTHORIZATION_CODE,
  AuthorizationServiceConfiguration,
  RedirectRequestHandler,
  AuthorizationNotifier,
  FetchRequestor,
  LocalStorageBackend,
  DefaultCrypto,
} from '@openid/appauth';
import { useNavigate } from 'react-router-dom';

import oidcConfig from '../../oidcConfig';
import { NoHashQueryStringUtils } from '../../noHashQueryStringUtils';
import useAuth from '../../hooks/useAuth';

const AuthenticationCallback = function (props) {
  const navigate = useNavigate();
  const auth = useAuth();

  // Made token request once received the authorization response or error
  useEffect(() => {
    const tokenHandler = new BaseTokenRequestHandler(new FetchRequestor());
    const authorizationHandler = new RedirectRequestHandler(
      new LocalStorageBackend(),
      new NoHashQueryStringUtils(),
      window.location,
      new DefaultCrypto(),
    );
    const notifier = new AuthorizationNotifier();
    authorizationHandler.setAuthorizationNotifier(notifier);
    notifier.setAuthorizationListener((request, response, error) => {
      if (response) {
        let extras = null;
        if (request && request.internal) {
          extras = {};
          extras.code_verifier = request.internal.code_verifier;
          extras.scope = oidcConfig.scope;
        }

        const tokenRequest = new TokenRequest({
          client_id: oidcConfig.clientId,
          redirect_uri: oidcConfig.redirectURL,
          grant_type: GRANT_TYPE_AUTHORIZATION_CODE,
          code: response.code,
          refresh_token: undefined,
          extras,
        });

        AuthorizationServiceConfiguration.fetchFromIssuer(oidcConfig.OPServer, new FetchRequestor())
          .then((configuration) => {
            return tokenHandler.performTokenRequest(configuration, tokenRequest);
          })
          .then((response) => {
            auth.setTokenResponse(response);
            navigate('/');
          })
          .catch((error) => {
            console.error('Response error:', error);
          });
      }
    });
    authorizationHandler.completeAuthorizationRequestIfPossible();
  }, []);

  return <div>Loading...</div>;
};

export default AuthenticationCallback;
